<script>
  import { FormatNumber } from "./libraries/formatNumber";
  export let current = 0;
  let inputValue = 0;

  
  let max = 6;
  let section;
  let price = 0;
  let email_marketing = null;
  let info_plan = null;
  let inputRangeElement = null;
  let main_container = null;
  let info_text_container = null;
  let selected_element = null;
  let selected_addon = {};
  let list = [];
  export let id = $$props.id ?? null;
  export let version = $$props.version ?? null;
  export let lang =
    (typeof ComponentSliderEmailMarketing !== "undefined" &&
      ComponentSliderEmailMarketing.lang) ??
    "";
  export let interval = $$props.interval ?? "month";
  export let onUpdate = function(){};
  interval = interval.toLowerCase();

  


  $: {
    if(inputValue){
      inputRangeElement.style.pointerEvents = "all";
      
      onUpdate({
        selected_addon: selected_addon,
        value: inputValue,
        selected: current,
        list: list,
      });
    }
  }


  /**Cuando el intervalo cambie*/
  function setIntervalPlan() {
    let interval_text = info_plan.querySelector(".interval");
    interval_text.innerText = lang[interval];
  }

  $: {
    if (interval || email_marketing !== null) {
      if(info_plan !== null && selected_element !== null){
        setIntervalPlan();
        getBall(selected_element);
      }
    }
  }

  $: {
    if (inputValue) {
      setBallById(inputValue);
    }
  }

  function changeInputValue(value){
    let { items } = email_marketing.response[0];
      let ball = section.querySelector(`.balls[data-price='${value}']`)
        .dataset.price;

      let pricePosition = items[ball - 1].price;
      let emailPosition = items[ball - 1].limit;
      let inputResults = section.querySelector("input.input-range-selector");
      inputValue = value;
      selected_addon = {
        price: pricePosition,
        limit: emailPosition,
        addonPlan: email_marketing.response[0].__id__,
      }
      inputResults.value = btoa(
        JSON.stringify(selected_addon)
      );
  }

  $: { 
    if (inputValue) {
      changeInputValue(inputValue)
    }
  }

  function getApiConnection() {
    fetch("https://app.3pod.io/api/getAddons/funnels")
      .then((response) => {
        return response.json();
      })
      .then((response) => {
        if ((response.status || "error") === "error") {
          console.error("Error al conectar con la API");
          return;
        }
        email_marketing = response;
        email_marketing.response[0].items.map((item, key) => {
          email_marketing.response[0].items[key].prices = {
            month: email_marketing.response[0].items[key].price,
            year: email_marketing.response[0].items[key].price * 12,
          };
        });
        list = email_marketing.response[0].items;
        let current_key = 0;
        let search_current = email_marketing.response[0].items.find(( item, key )=>{
          if(item.limit === current){
            current_key = key+1;
            return item;
          }
        })

        if(typeof search_current !== "undefined"){
          current = current_key
        }
        
        
        setTimeout(() => {
          let key = 0;
          key = current;
          selected_element = main_container.querySelector(`.balls[data-item='${key}']`);
        }, 500);
        
      });
  }
  
  getApiConnection();
  


  function setBallById(id){
    let node = section.querySelector(`.balls[data-item='${id}']`);
    getBall(node);
  }
  
  function getBall(node) {
    selected_element = node;
    let position = node.dataset.position;
    let item = node.dataset.item;
    
    function setPositionRange(){
      let nodes = [...section.querySelectorAll(".balls")];
      nodes.map((node) => {
        node.classList.remove("selected");
      });
      node.classList.add("selected");
    }
    setPositionRange();


    let priceElement = info_text_container.querySelector(".price-marketing");
    let suscribersMarketingElement = info_text_container.querySelector(".subscribers-marketing");
   
    let totalPrice = email_marketing.response[0].items[position].prices[interval];
    let limit = email_marketing.response[0].items[position].limit;
    
    priceElement.innerText = `${FormatNumber(totalPrice, true)}`;
    suscribersMarketingElement.innerText = `${FormatNumber(limit, false)}`;

    
    changeInputValue(+(item))
  }
</script>

{#if email_marketing !== null}
  <div class="wrapper {version}" bind:this="{main_container}">
    <section class="section-content">
      <div class="container-email" bind:this={price}>
        <div class="email">
          <img
            class="email-icon"
            src="https://raw.githubusercontent.com/Yerikmiller/3pod-us.github.io/master/src/email-icon-3pod.svg"
            alt="Email Marketing Icon"
          />
        </div>
        <div class="info" bind:this={info_plan}>
          <p>
            <span class="info_text_container" bind:this="{info_text_container}">
              {@html lang.benefits}
            </span>
          </p>
        </div>
      </div>
    </section>
    <section bind:this={section} class="section-balls {version}">
      <div class="container-balls ">
        <input type="hidden" class="input-range-selector" value="" />
        <input
          class="range"
          {id}
          type="range"
          bind:value={inputValue}
          bind:this={inputRangeElement}
          min="1"
          {max}
          step="1"
        />

        <div class="container-ball-child">
          {#each email_marketing.response[0].items as item, key}
            <p
              on:click={function () {
                getBall(this);
              }}
              class="ball{key} balls {key === 0 && 'selected'}"
              data-item={key + 1}
              data-limit={item.limit}
              data-price={key + 1}
              data-position={key}
            >
              <span class="limit-span">
                +{FormatNumber(item.limit, false)}
              </span>
              <span class="hiddenSpan"></span>
            </p>
          {/each}
        </div>
      </div>
    </section>
  </div>
{/if}
<style>
  :global(body) {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Montserrat", "Montserrat", sans-serif;
  }

  :global(.wrapper.default) {
    height: 8.125rem;
  }

  section {
    margin: 1.25rem 0;
    margin-bottom: 1.75rem;
  }

  .section-balls {
    display: flex;
    justify-content: center;
  }
  .section-balls.recommended .balls:global(.selected span) {
    color: #fff;
    font-weight: 800;
  }
  .balls {
    content: "";
    position: absolute;
    display: inline-flex;
    top: 0.125rem;
    background-color: #fff;
    border-radius: 0.625rem;
    width: 0.75rem;
    height: 0.75rem;
    border: 0.0187rem solid #e0e7ec;
    justify-content: center;
    cursor: pointer;
  }

  .balls:global(.selected) {
    background-color: #344fff;
    transform: scale(1.125);
  }
  .balls:global(.selected span) {
    color: #344fff;
    font-weight: 600;
  }

  .wrapper.recommended .container-email :global(.price-marketing) {
    color: #fde203;
    font-weight: 700;
    font-size: 0.875rem;
  }
  .wrapper .container-email :global(.subscribers-marketing) {
    font-weight: 500;
    color: #415b76;
    font-size: 0.875rem;
  }
  .wrapper.recommended .container-email :global(.subscribers-marketing) {
    color: #fff;
  }
  .wrapper.recommended .container-email {
    color: #fff;
  }

  .section-balls.recommended .container-balls:global(.container-balls) {
    margin-bottom: 5rem;
  }

  .wrapper.default
    .container-email :global(.subscribers-marketing) {
    font-weight: 500;
    color: #415b76;
    font-size: 0.875rem;
  }

  .wrapper.default .container-email :global(.email) {
    background-color: rgb(52 79 255 / 7%);
  }

  .wrapper.default .container-email :global(.info) {
    color: #415b76;
  }

  .wrapper.default .container-email :global(.bold-marketing) {
    color: #415b76;
  }

  .wrapper.default .container-email :global(.price-marketing) {
    font-weight: bolder;
    font-weight: 500;
    color: #415b76;
    font-size: 14px;
  }

  input {
    -webkit-appearance: none;
    background-color: #fde203;
    border-radius: 10%;
    height: 0.5rem;
    position: absolute;
    top: 0;
    width: 100%;
    border-radius: 20px;
    cursor: pointer;
  }

  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 1.0625rem;
    height: 1.0625rem;
    border-radius: 50%;
    z-index: 10000;
    position: sticky;
  }

  .section-balls.recommended span {
    color: #fff;
  }

  span.limit-span {
    position: relative;
    margin-top: 1rem;
    font-size: 11px;
    color: #aab8c2; 
  }

  .container-balls {
    position: relative;
    width: 15.625rem;
  }

  .container-ball-child {
    position: absolute;
    top: -0.3rem;
  }

  .ball0 {
    left: 0px;
  }

  .ball1 {
    left: 3.125rem;
  }

  .ball2 {
    left: 5.75rem;
  }

  .ball3 {
    left: 8.875rem;
  }

  .ball4 {
    left: 11.75rem;
  }

  .ball5 {
    left: 14.9375rem;
  }

  .container-email {
    display: flex;
    justify-content: flex-start;
    width: 100%;
  }

  .container-email .email {
    background-color: #fde203;
    width: 3.25rem;
    height: 3.25rem;
    border-radius: 50%;
    margin-right: 0.5rem;
  }

  .container-email .info {
    width: 80%;
    font-size: 0.875rem;
    line-height: 1.5625rem;
  }

  .bold-marketing {
    font-weight: 500;
    color: #fff;
    font-size: 0.875rem;
  }

  .email {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .email-icon {
    width: 2.75rem;
    height: 2.75rem;
  }

  .hiddenSpan {
    position: absolute;
    top: -18px;
    z-index: 10000;
    width: 0.75rem;
    height: 0.75rem;
  }
</style>
